.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  padding-bottom: 100px; /* Adjust this value based on your footer's height */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#projects,
#experience {
  min-height: 100vh;
}

#hero {
  /* take full viewport minus header */
  min-height: calc(100vh - var(--header-h));

  display: flex;
  align-items: center; /* vertical centring in the visible space */
  justify-content: center; /* horizontal centring (optional) */

  /* let smooth-scroll stop just below the sticky header */
  scroll-margin-top: var(--header-h);
}
